import ExpireTime from "@/components/ExpireTime/ExpireTime.vue";
export default {
	data(){
		return {
			expireTime:0,
			isShow:false,
			address:{},
			goods:[],
			totalPrice:0,
			freight:0,
			orderInfo:{
				add_time:"",
				message:"",
				order_sn:"",
				order_status:""
			},
			isOvertime:false,
			someObject:{
				info:""
			},


		}
	},
	components:{
		ExpireTime
	},
	onLoad(e){
		this.$request.GET({
			url:this.$api.apiUrl.GET_ORDER_FIND+"/"+e.id,
		}).then(res=>{
			if(res.code !== 200){this.$api.msg(res.msg); return false}
			let result = res.data
			// console.log(result);
			
			// 过期时间,非待付款状态过滤
			this.expireTime = result.expire_time ? result.expire_time : null;
			// 地址
			this.address = result.address;
			// 详情
			this.orderInfo = result.order;
			// 商品信息
			this.goods = result.goods;
			// 订单总价
			for (const key of this.goods) {
				this.totalPrice += Number(key.goods_price) * key.goods_num
				this.freight = this.totalPrice>300 ? 0 : 6;
			}
			// 数据请求到后在显示组件
			this.isShow = true;
		})
	},
	methods: {
		callback(e){
			this.$set(this.someObject,"info",e)
		},
		payment(){
			this.isOvertime = true;
			uni.login({
				provider: 'weixin',
				success:  (loginRes)=> {
					this.$request.POST({
						url:this.$api.apiUrl.POST_PAY_INDEX,
						data:{
							order_sn:this.orderInfo.order_sn,
							code:loginRes.code
						}
					}).then(res=>{
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: String(res.data.timeStamp),
							nonceStr: res.data.nonceStr,
							package: res.data.package,
							signType: 'MD5',
							paySign: res.data.paySign,
							success: (res)=> {
								uni.showToast({
									title:"支付成功",
									success:()=>{
										uni.navigateTo({
											url:"/pages/mys/order/order?state=2"
										})
									}
								})
							},
							fail: (err)=>{
								this.$api.msg('支付失败')
								this.isOvertime = false;
								// console.log('支付失败' + JSON.stringify(err));
							}
						});
					})
				},
			});			
		},
		// 确认收货
		searchLogistics(id){
			this.$request.POST({
				url:this.$api.apiUrl.POST_ORDER_LOGISTICS,
				data:{
					order_id:id
				}
			}).then(res=>{
				const result = res.errMsg;
				uni.showModal({
					content: result.shipping_code+" : "+result.shipping_name,
					showCancel: false,
					confirmText: "确定"
				})
			})
		},
	}
}
